//variables for site-wide themes and styling
$font-stack:  fira-sans-2, sans-serif;
$primary-color: white;
$base-color: black;
$heading-size: 7rem;
$sub-heading-size: 2rem;
$full-width: 100vw;
$half-width: 50vw;
$third-width: 33.33vw;
$full-height: 100vh;
$half-height: 50vh;
$third-height: 33.33vh;
$linktransition: all .5s cubic-bezier(1,.25,0,.75) 0s;


//set all defaults to normal font-weight
h1, h2, h3, h4, h5, h6 {
  font-weight: normal; }

body {
  background-color: $base-color;
  margin: 0;
  color: $primary-color;
  font-family: fira-sans-2, sans-serif; }

a {
  text-decoration: none;
  color: $primary-color; }

#logo {
  display: flex;
  height: 40vh;
  width: $full-width; }

.fullScreen {
  width: $full-width;
  height: 100vh; }

.smallTransition {
  min-height: $third-height;
  width: $full-width;
  text-align: center;
  p {
    margin: auto;
    margin: 1vw 0 -2vw;
    font-size: $sub-heading-size;
    line-height: $third-height; } }

#portfolioTransition {
  background-image: url("/../app/images/topimage.png");
  img {
    height: 2vw;
    padding-bottom: 3vw;
    margin-top: -6vw; } }

#portfolio {
  width: $full-width;
  height: 20vw;
  position: absolute;
  h1 {
    font-family: $font-stack;
    font-size: $heading-size;
    padding-left: 5vw;
    line-height: 1vw; }
  img {
    padding-left: 5vw; } }

.largeTransition {
  width: 100vw;
  height: 40vw; }

.sectionUnderline {
  width: 20vw; }

#topimage {
  height: 50vh;
  width: 100vw;
  background-image: url("/../app/images/topimage.png");
  background-size: cover;
  display: flex;
  align-items: center; }

#topGrid {
  height: $half-height;
  width: $full-width; }

.button {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 2em;
  text-align: center;
  margin: auto;
  align-items: center;
  padding: 0 1vw 0.75vw 1vw;
  a {
    font-size: $sub-heading-size;
    &::before {
      content: "";
      position: absolute;
      width: 10vw;
      height: 1px;
      bottom: 20%;
      margin: auto;
      background-color: $primary-color;
      visibility: hidden;
      -webkit-transform: scaleX(0);
      transform: scaleX(0);
      -webkit-transition: $linktransition;
      align: center; }
    &:hover::before {
      visibility: visible;
      -webkit-transform: scaleX(1);
      transform: scaleX(1); } } }




#photography {
  height: 50vh;
  width: 33.33vw;
  background-image: url("../app/images/photography.PNG");
  background-size: cover;
  display: flex;
  align-items: center;
  margin: auto; }

#design {
  height: 50vh;
  width: 33.33vw;
  background-image: url("../app/images/design.JPG");
  background-size: cover;
  display: flex;
  align-items: center;
  margin: auto; }

#web {
  height: 50vh;
  width: 33.33vw;
  background-image: url("../app/images/web.png");
  background-size: cover;
  display: flex;
  align-items: center;
  margin: auto; }

.row {
  display: flex;
  flex-wrap: wrap;
  padding: 0 0;
  align-items: center; }

.column {
  flex: 33.33%;
  max-width: 33.33%;
  padding: 0 0;
  align: center; }

.column img {
  margin-top: 0;
  vertical-align: middle;
  width: 100%; }
